// app/api/wixClientServer.js (or inside API route in the `pages/api/` directory)
import { createClient, OAuthStrategy } from "@wix/sdk";
import { products, collections } from "@wix/stores";
import { cookies } from "next/headers"; // This works only in server components

export const wixClientServer = async () => {
  let refreshToken = null;
  
  try {
    const cookiesStore = cookies();
    refreshToken = JSON.parse(cookiesStore.get("refreshToken")?.value || "{}");    
  } catch (err) {
    console.error("Error retrieving refresh token:", err);
  }

  // Check if the refreshToken exists and is valid
  if (!refreshToken || !refreshToken.value) {
    throw new Error("Missing or invalid refresh token.");
  }

  const wixClient = createClient({
    modules: {
      products,
      collections
    },
    auth: OAuthStrategy({
      clientId: process.env.NEXT_PUBLIC_WIX_CLIENT_ID,
      tokens: {
        refreshToken,
        accessToken: { value: "", expiresAt: 0 }
      }
    }),
  });

  return wixClient;
};
